import React from "react";
import { Row, Col, Container } from "react-bootstrap";

//import CSS
import './team.css';

//import image assets
import brandon from "../../assets/team/theteam/1.png"
import hamza from "../../assets/team/theteam/2.png"
import davyy from '../../assets/team/theteam/3.png'
import cj from '../../assets/team/theteam/4.png'
import nick from '../../assets/team/theteam/5.png'
import muzlimin from '../../assets/team/theteam/1.png'

//import Component
import TeamMember from "./teammember";
class Team extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            teams: [
                {
                    image: brandon,
                    title: 'Project Lead',
                    name: "Name",
                    link: ""
                },
                {
                    image: hamza,
                    title: 'Artist',
                    name: "Name",
                    link: null
                }
            ]
        }
    }

    render() {
        return (
            <div className='team-control' id='team'>
                <Container>
                    <header style={{fontStyle: "normal", color:"black"}}> MEET OUR TEAM! </header>
                    <Row>
                        {
                            this.state.teams.map((member, index) => {
                                return(
                                    <Col className="team-control-card" md={6} xs={12} >
                                        <TeamMember 
                                            imageUrl={member.image}
                                            title={member.title}
                                            name={member.name} 
                                        />
                                    </Col>
                                );
                            })
                        }
                    </Row> 
                </Container>
            </div>
        );
    }
}

export default Team