import React from "react";
import { Col, Container, Row } from "react-bootstrap";

//import CSS
import './faqmap.css';

//import Assets
import roadmap from '../../assets/Roadmap.mp4';

//import Component
import RoadMapItem from "./faqmapitem";
class RoadMap extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            roadmaps: [
                {
                    tag: '1',
                    title: "Who are you guys?",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '1',
                    title: "What’s an NFT?",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '1',
                    title: "Where does my NFT go after I purchase a Boss Bunnies NFT?",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '1',
                    title: "How much?",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '1',
                    title: "What about breeding? ",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '1',
                    title: "What can I do with my Boss Bunnies?",
                    content: "Lorem Ipsum"
                }
                
            ]
        }
    }

    render() {
        return (
            <div className='roadmap-control' id='faq' style={{ backgroundColor: "#F5EED2", backgroundImage: "none" }}>
                <Container style={{ backgroundColor: "#F5EED2", paddingTop: 0, paddingBottom: 100, border: "solid #40210a 5px", borderRadius: 20 }}>
                    <header style={{fontStyle: "normal", color:"#40210a", textAlign: "center" }}><span></span> <br/> FAQ's</header>
                    <Row style={{ paddingTop: 0}}>
                        <Col>
                            {
                                this.state.roadmaps.map((item, index) => {
                                    return (
                                        <RoadMapItem style={{ color: "black" }} key={index}
                                            tag={item.tag}
                                            title={item.title}
                                            content={item.content}
                                            content2={item.content2}
                                            content3={item.content3}
                                            content4={item.content4}
                                            content5={item.content5}
                                            content6={item.content6}
                                             />
                                    );
                                })
                            }
                        </Col>
                    </Row>
                </Container>
                <br/>
            </div>

        );
    }
}

export default RoadMap;