import React from "react";
import { Col, Container, Row } from "react-bootstrap";

//import CSS
import './roadmap.css';
import NewRoadMap from '../newroadmap/newroadmap';

//import Assets
import roadmap from '../../assets/Roadmap.mp4';

//import Component
import RoadMapItem from "./roadmapitem";
class RoadMap extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            roadmaps: [
                {
                    tag: '0%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                },
                {
                    tag: '20%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                }
                ,
                {
                    tag: '40%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                }
                ,
                {
                    tag: '60%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                }
                ,
                {
                    tag: '80%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                }
                ,
                {
                    tag: '100%',
                    title: "Lorem Ipsum",
                    content: "Lorem Ipsum"
                }
            ]
        }
    }

    render() {
        return (
            <div className='roadmap-control' id='roadmap' style={{ paddingTop: 50, paddingBottom: 0 }}>
                <Container style={{ backgroundColor: "none", padding: 30, borderRadius: 20, textAlign: "center", border: "solid #40210a 5px" }}>
                    <header style={{ fontWeight: 900, color: "#40210a" }}><span style={{color: "#40210a"}}>BOSS BUNNIES</span> <br /> ROADMAP</header>
                    {/* <p className='roadmap-description'>
                        Welcome to the Dead Banditz Community.<br />Now that you have jumped on the wagon, strap in for a life changing ride!
                    </p> */}
                    <img  className="roadimage"style={{border:"solid 5px black", borderRadius: 20}}src="https://github.com/DeadBanditz/banditzsite/blob/main/src/assets/wood.png?raw=true" />
                    <Row style={{ paddingTop: 30}}>
                        <Col style={{ color: "white"}}>
                            {
                                this.state.roadmaps.map((item, index) => {
                                    return (
                                        <RoadMapItem style={{color: "black" }} key={index}
                                            tag={item.tag}
                                            title={item.title}
                                            content={item.content}
                                            content2={item.content2}
                                            content3={item.content3}
                                            content4={item.content4}
                                            content5={item.content5}
                                            content6={item.content6}
                                        />
                                    );
                                })
                            }
                        </Col>
                    </Row>
                    {/* <NewRoadMap /> */}
                </Container>
                <br/>
            </div>

        );
    }
}

export default RoadMap;